.profile-container {
  background-color: #1A1A2E;
}

.profile-header {
  text-align: center;
}

.profile-background {
  width: 100%;
  height: 22.5vh;
}
  
.header-overlay {
  position: absolute;
  top: 5px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0 0px;
  align-items: center;
}
.token-info {
  font-size: 14px;
  color: #ffffff;
  align-items: center;
}

.sett-icon {
  position: absolute;
  top: 15px;
  right: 20px;
  width: 36px;
  height: 36px;
  background-color: #ff4385;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.sett-icon img {
  width: 80%;
  height: 80%;
  object-fit: contain; /* Іконка буде масштабуватись правильно */
  filter: invert(1); /* Робимо іконку білою */
  overflow: hidden;

}





  .profile-image-container {
    position: absolute;
    top: 12%; 
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  .profile-image {
    border-radius: 50%;
    width: 119px;
    height: 119px;
    border: 10PX solid #1A1A2E;
  }

.user-info {
  margin-top: -10%;
}
  .user-info h2 {
    font-size: 20px;
  }
  
  .user-info p {
    font-size: 14px;
    color: #cccccc;
  }

  .profile-bio {
    background-color: #1A1A2E;
    padding: 0 5%;
    margin-top: 20px;
    border-radius: 8px;
    font-size: 14px;
}

textarea {
    width: 100%;
    height: 60px;
    background-color: #292a3b;
    color: white;
    border: 1px solid #1A1A2E; /* Рожевий бордер */
    padding: 10px; /* Внутрішній відступ */
    font-size: 14px;
    border-radius: 2px;
    outline: none;
    resize: none;
    box-sizing: border-box; /* Забезпечує коректне обчислення ширини/висоти */
}

textarea:focus {
    border-color: #ff6f95; /* Зміна кольору бордера при фокусі */
}

.status-dropdown {
  margin-top: 10px;
  padding: 0 5%; /* Відступ з боків */
}

.status-dropdown select {
  width: 100%;
  padding: 15px;
  background-color: #191A2E;
  border: 1px solid #1A1A2E;
  color: white;
  font-size: 16px;
  border-radius: 8px;
  appearance: none;
  background-image: url('/public/images/select.png'); /* Іконка стрілки */
  background-repeat: no-repeat;
  background-position: right 15px center;
  background-size: 16px;
  cursor: pointer;
}

/* Стиль для опцій у випадаючому списку */
.status-dropdown select option {
  background-color: #191A2E;
  color: white;
  padding: 15px;
}

/* Підсвічування при наведенні */
.status-dropdown select option:hover {
  background-color: #FF4385; /* Рожевий фон при наведенні */
  color: white;
}

/* Стиль для обраного варіанту */
.status-dropdown select option:checked {
  background-color: #FF4385; /* Колір активного елементу */
  color: white;
}