.bottom-nav {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: space-around;
    background-color: #191A2C; /* Колір фону */
    padding: 0;
    z-index: 1002; /* Переконайтесь, що меню завжди зверху */
  }
  
  .nav-btn {
    background: none;
    border: none;
    padding: 7px;
    outline: none;
    cursor: pointer;
    position: relative;
  }
  
  .nav-btn img {
    width: 30px;
    height: 30px;
    filter: grayscale(100%); /* Іконки за замовчуванням сірі */
    opacity: 0.5; /* Прозорість 50% для неактивних іконок */
    transition: all 0.3s ease;
  }
  
  .nav-btn.active img {
    filter: none; /* Прибираємо сірий фільтр для активної іконки */
    filter: brightness(0) invert(1); /* Робимо лого чисто білим */
    opacity: 1; /* Повна непрозорість для активних іконок */
    width: 40px; /* Збільшуємо ширину іконки */
    height: 40px; /* Збільшуємо висоту іконки */
  }
  
  .nav-btn.active::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80px;
    height: 80px;
    background-color: #FF4385; /* Колір кружка активної кнопки */
    border-radius: 50%;
    z-index: -1; /* Кружок за іконкою */
  }