.search-container {
    align-items: center;
    
}

.token-up {
    display: flex;
    justify-content: space-between; /* Іконка зліва, текст справа */
    align-items: center;
    background-image: url('/public/images/222.png'); /* Додаємо фон. Переконайтеся, що шлях правильний */
    background-size: cover; /* Розтягуємо фон, щоб покрити весь контейнер */
    background-position: center; /* Центруємо фон */
    padding: 10px; /* Відступи всередині */

}
.location-button {
    background-color: transparent;
    border: none;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer; /* Додаємо курсор на кнопку */
}

.location-icon{
    background-color: #2e2e3e;
    border-radius: 50%;
    width: 42px;
    height: 42px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color 0.3s ease;
}

.location-icon img {
    width: 22px;
    height: 22px;
    filter: grayscale(100%);
}

.location-text {
    color: white;
     font-size: 16px;
    font-weight: bold;
    text-align: right;
    padding-right: 10px;
}

.photo-progres-barom {
    display: flex;
    justify-content: space-between; /* Розподіляє елементи рівномірно */
    position: absolute;
    top: 9%; /* Відступ від нижнього краю */
    width: 100%; /* Розтягує на всю ширину */
    box-sizing: border-box; /* Забезпечує, що padding враховується в загальній ширині */
    z-index: 1003;
}

.progress-bar-step {
    flex-grow: 1; /* Розтягує кожен елемент рівномірно */
    height: 4px;
    background-color: #3d3d5c;
    margin: 0 4px; /* Невеликий відступ між елементами */
    border-radius: 2px;
}
  
.progress-bar-step.active {
    background-color: #FF4385;
}

.image-container-profile {
    width: 100%; /* Ширина контейнера 100% відносно батьківського елемента */
    height: 80%; /* Висота зображення займає весь контейнер */
    max-height: 60vh; /* Обмежуємо висоту зображення відносно висоти екрану */
    padding: 0; /* Забираємо відступи всередині контейнера */ 
    display: flex;
    justify-content: center; /* Центруємо зображення по горизонталі */
    object-fit: cover; /* Обрізає зображення, якщо воно не відповідає співвідношенню сторін контейнера */ 
}

.image-container-profile img {
    width: 100%; /* Зображення буде займати всю ширину контейнера */
    height: auto; /* Висота автоматично підлаштовується під ширину */
    object-fit: cover; /* Фотографія буде обрізана по горизонталі або вертикалі, якщо співвідношення сторін відрізняється */
    max-height: 55vh;
    object-fit: cover; /* Забезпечує, щоб зображення не стискалося, а просто обрізалося */
}


.profile-info {
    text-align: center;
    margin-top: 5%;
}
  
.profile-name {
    color: #ffffff;
    font-size: 24px;
    font-weight: bold;
}
  
.profile-details {
    color: #a0a0a0;
    font-size: 15px;
}
  
.profile-description {
    color: #ffffff;
    font-size: 15px;
    margin-top: 10px;
    padding: 0 20px;
    margin-bottom: 10px;
    max-height: 100px; /* Обмежуємо висоту тексту */
    overflow-y: auto; /* Додаємо прокручування, якщо текст перевищує висоту */
    padding-bottom: 40%; /* Додаємо відступ знизу, щоб текст не підходив занадто близько до середніх кнопок */
    z-index: 0; /* Забезпечуємо, що текст завжди буде під кнопками */
}

.middle-nav {
    display: flex;
    position: absolute; /* Додаємо абсолютне позиціювання */
    bottom: 10%; /* Відстань від нижнього краю 15% */
    justify-content: space-around; /* Вирівнюємо кнопки рівномірно */
    align-items: center; /* Вирівнюємо по вертикалі */
    gap: 10px; /* Відстань між кнопками */
    left: 50%; 
    transform: translateX(-50%);   
    z-index: 1001; /* Щоб кнопки були над іншими елементами */
}

  /* Стилі для кнопок */
  .round-btn, .round-btn0 {
    position: relative;
    border: none;
    background-color: #2e2e3e; /* Фон для кнопок */
    border-radius: 50%; /* Кругла форма */
    width: 50px; /* Ширина кнопки */
    height: 50px; /* Висота кнопки */
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.3s ease;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Легка тінь навколо кнопки */
  }
  
  .round-btn1, .round-btn2 {
    position: relative;
    border: none;
    background-color: #2e2e3e; /* Фон для кнопок */
    border-radius: 50%; /* Кругла форма */
    width: 65px; /* Ширина кнопки */
    height: 65px; /* Висота кнопки */
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.3s ease;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Легка тінь навколо кнопки */
  }
  
  .round-btn.inactive, .round-btn1.inactive, .round-btn0.inactive {
    background-color: #3e3e4e; /* Сірий фон для неактивної кнопки */
    box-shadow: 0 8px 15px rgba(62, 62, 78, 0.4); /* Додайте тінь для неактивної кнопки з меншою інтенсивністю */
    filter: grayscale(70%);
  }
  
  .round-btn.inactive img, .round-btn1.inactive img, .round-btn0.inactive img {
    filter: grayscale(70%); /* Легкий сірий фільтр для неактивних кнопок */
  }
  
  .round-btn img {
    width: 26px;
    height: 26px;
    filter: grayscale(100%); /* Иконки по умолчанию серые */
    transition: all 0.3s ease;
  }
  .round-btn1 img {
    width: 34px;
    height: 34px;
    filter: grayscale(100%); /* Иконки по умолчанию серые */
    transition: all 0.3s ease;
  }
  .round-btn0 img {
    width: 17px;
    height: 24px;
    filter: grayscale(100%); /* Иконки по умолчанию серые */
    transition: all 0.3s ease;
  }
  .round-btn2 img {
    width: 34px;
    height: 29.3px;
    filter: grayscale(100%); /* Иконки по умолчанию серые */
    transition: all 0.3s ease;
  }
  
  .round-btn.active , .round-btn0.active , .round-btn1.active, .round-btn2.active {
    background-color: #FF4385; /* Цвет кружка активной кнопки */
    box-shadow: 0 8px 15px rgba(240, 98, 146, 0.4); /* Рожевий відтінок тіні для активної кнопки */
  }

  .modal-overlay {
    position: fixed;
    top: 6.9%;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #1A1A2E;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1004;
}

.modal-content {
    padding: 0px;
    width: 100%;
    height: 10%;
    z-index: 1001;
    text-align: center;
    height: 50vh;
    
}

.modal-image {
    width: 70%;
    height: auto;
    margin-bottom: 15px;
    align-items: center;
  }
  
  h2 {
    color: white;
    font-size: 20px;
    margin-bottom: 10px;
  }
  
  p {
    color: white;
    font-size: 15px;
    margin-bottom: 20px;
    opacity: 83%;
  }
  
  .modal-button {
    background-color: #ff5681;
    border: none;
    color: #000;
    padding: 23px 75px;
    font-size: 16px;
    font-weight: 750;
    border-radius: 34px;
    cursor: pointer;
  }
  
  .modal-button:hover {
    background-color: #ff6f95;
  }
/* Модальне вікно */
.second-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5); /* Затемнення фону */
    display: flex;
    justify-content: flex-end; /* Вирівнюємо по вертикалі вниз */
    align-items: flex-end; /* Щоб контент знаходився внизу */
    z-index: 1011;
}

.second-modal-content {
    background-color: #191A2C;
    border-radius: 10px 10px 0 0;
    width: 100%;
    max-width: 400px;
    margin: 0;
    z-index: 1011;
}

.distance-header {
    font-size: 20px; /* Розмір шрифту */
    color: white; /* Колір тексту */
    text-align: center; /* Вирівнювання по центру */
    margin-bottom: 10px; /* Відступ від тексту до кнопок */
    font-weight: bold; /* Жирний текст */
    margin-top: 10px;
}

.distance-option {
    background-color: #282c3a;
    color: white;
    font-size: 18px; /* Розмір шрифту */
    padding: 25px; /* Відступи всередині кнопки */
    margin-bottom: 15px; /* Відступ між кнопками */
    cursor: pointer;
    transition: background-color 0.3s ease;
    text-align: center; /* Центруємо текст в кнопці */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* Тінь */
    width: 90%; /* Зменшимо ширину для трохи більше відступів з боків */
    margin: 0 auto; /* Центруємо кнопки */
}

.distance-option:hover {
    background-color: #3a3f50; /* Колір кнопки при наведенні */
}
